import React from 'react';
import { navigate, useRoutes } from 'hookrouter';
import DrillDown from './DrillDown';
import VinSearch from './VinSearch';
import NotFound from './NotFound';
import Login from './Login';
import Logout from './Logout';

import bbService from '../services/bbService';
import mmrService from '../services/mmrService';
import kbbService from '../services/kbbService';
import { bbModel, mmrModel, kbbModel } from '../models';

import { AuthPropTypes } from '../propTypes';

const Router = props => {
  /* eslint-disable */
  const routes = {
    '/': () => (
      <DrillDown
        model={bbModel}
        service={bbService()}
        title="Black Book Drilldown"
      />
    ),
    '/mmr': () => (
      <DrillDown
        model={mmrModel}
        service={mmrService()}
        title="Manheim Drilldown"
      />
    ),
    '/kbb': () => (
      <DrillDown
        model={kbbModel}
        service={kbbService()}
        title="Kelly Blue Book® Drilldown"
      />
    ),
    '/vin': () => (
      <VinSearch mmrService={mmrService()} bbService={bbService()} kbbService={kbbService()} />
    ),
    '/login': () => <Login {...props} />,
    '/logout': () => <Logout {...props} />
  };
  /* eslint-enable */

  const routeResult = useRoutes(routes);

  if (!localStorage.getItem('access_token')) {
    navigate('/login');
  }

  return routeResult || <NotFound />;
};

Router.propTypes = AuthPropTypes;

export default Router;
