import React, { useState, useEffect } from 'react';
import NumericInput from '@cx/ui/NumericInput';
import ChainedSelectInput from '@cx/ui/ChainedSelectInput';
import Button from '@cx/ui/Button';
import SubmitButton from '@cx/ui/SubmitButton';

import { DrillDownSelectsPropTypes } from '../propTypes';

const DrilldownSelects = props => {
  const { model, service, updateResults, years, clear } = props;

  const [state, updateState] = useState(model(years, service));

  /* eslint-disable */
  const setState = (newState, cb = null) => {
    updateState({
      ...state,
      ...newState
    });
    cb && cb();
  };

  useEffect(() => {
    updateResults({ results: {} });
  }, [service]);
  /* eslint-enable */

  const onChange = value => {
    setState({ value });
  };

  const onTextInputChange = ({ target: { name, value } }) => {
    setState({ [name]: value });
  };

  const onSubmitClick = () => {
    updateResults({ searching: true });
    service.getValuations(
      { ...state.value, mileage: state.mileage, zip: state.zip },
      updateResults
    );
  };

  const onClearClick = () => {
    clear();
  };

  const onFocus = () => {
    // does not seem possible to get the input using ref
    document.querySelector('#MileageInput-maskedInput').select();
  };

  return (
    <div>
      <ChainedSelectInput
        htmlId="ChainedSelects"
        items={Array.from(state.items)}
        value={state.value}
        onChange={onChange}
      />
      <NumericInput
        autoInsertCommas
        htmlId="MileageInput"
        name="mileage"
        label="Mileage"
        onChange={onTextInputChange}
        onFocus={onFocus}
        value={state.mileage}
        maxLength={20}
      />
      {service.name === 'kbbService' && (
        <NumericInput
          required
          htmlId="ZipInput"
          name="zip"
          label="Zip"
          onChange={onTextInputChange}
          value={state.zip}
          maxLength={5}
        />
      )}
      <SubmitButton
        htmlId="SubmitButton"
        className="pull-right"
        onClick={onSubmitClick}
      >
        Submit
      </SubmitButton>
      <Button htmlId="ResetButton" onClick={onClearClick}>
        Reset
      </Button>
    </div>
  );
};

DrilldownSelects.propTypes = DrillDownSelectsPropTypes;

export default DrilldownSelects;
