import toast from 'toasted-notes';

import { makeHeaders, statusCheck } from '../util';

export default () => {
  const accessToken = localStorage.getItem('access_token');
  const valuationsBaseUrl = localStorage.getItem('gateway_url');
  return {
    name: 'mmrService',
    getYears: callback => {
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/mmr/details/years`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json =>
          callback({
            years: json.years.map(year => ({ value: year, label: year }))
          })
        )
        .catch(() =>
          toast.notify('Error fetching years. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getMakes: ({ year }, callback) => {
      if (!year) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/mmr/details/${year}/makes`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.makes.length === 0) {
            callback([]);
            toast.notify('No makes found for this year.', {
              position: 'bottom'
            });
            return;
          }
          callback(json.makes.map(make => ({ value: make, label: make })));
        })
        .catch(() =>
          toast.notify('Error fetching makes. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getModels: ({ year, make }, callback) => {
      if (!make) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/mmr/details/${year}/vehicle?make=${make}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.items.length === 0) {
            callback([]);
            toast.notify('No models found for this make.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            [...new Set(json.items.map(item => item.description.model))]
              .sort()
              .map(model => ({ value: model, label: model }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching models. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getSeries: ({ year, make, model, style }, callback) => {
      if (!style) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/mmr/details/${year}/vehicle?make=${make}&model=${model}&trim=${style}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.items.length === 0) {
            callback([]);
            toast.notify('No series found for this style.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            [
              ...new Set(
                json.items.map(
                  item => item.description.subSeries || item.description.trim
                )
              )
            ]
              .sort()
              .map(series => ({ value: series, label: series }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching series. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getStyles: ({ year, make, model }, callback) => {
      if (!model) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/mmr/details/${year}/vehicle?make=${make}&model=${model}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.items.length === 0) {
            callback([]);
            toast.notify('No styles found for this model.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            [...new Set(json.items.map(item => item.description.trim))]
              .sort()
              .map(style => ({ value: style, label: style }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching styles. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getValuations: (
      { year, make, model, series, style, mileage },
      callback
    ) => {
      if (!style) {
        toast.notify('Missing style.', {
          position: 'bottom'
        });
        callback({ searching: false });
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/mmr/details/${year}/vehicle?make=${make}&model=${model}&trim=${style}&mileage=${mileage}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          const findBySubSeries = json.items.filter(
            item => item.description.subSeries === series
          );
          if (findBySubSeries.length === 0 && json.items.length === 0) {
            toast.notify('Manheim returned no results.', {
              position: 'bottom'
            });
            callback({ searching: false });
            return;
          }
          callback({
            searching: false,
            results: (findBySubSeries.length > 0
              ? findBySubSeries
              : json.items
            ).map(item => ({
              year: Number(year),
              make,
              model,
              series: '',
              style,
              extraCleanValue: null,
              cleanValue: item.wholesale.above,
              averageValue: item.wholesale.average,
              roughValue: item.wholesale.below,
              provider: 'Manheim Market Research'
            }))[0]
          });
        })
        .catch(() => {
          callback({ searching: false });
          toast.notify('Error fetching values. Please try again later.', {
            position: 'bottom'
          });
        });
    },
    getValuationsByVin: ({ vin, mileage }) => {
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/valuations/mmr/vin/${vin}?mileage=${mileage}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      );
    }
  };
};
