import React, { useState } from 'react';
import Grid from '@cx/ui/Grid';
import Col from '@cx/ui/Col';
import Row from '@cx/ui/Row';
import VinInput from '@cx/ui/VinInput';
import NumericInput from '@cx/ui/NumericInput';
import Button from '@cx/ui/Button';
import SubmitButton from '@cx/ui/SubmitButton';
import LoadingIndicator from '@cx/ui/LoadingIndicator';
import Panel from 'react-bootstrap/lib/Panel';
import toast from 'toasted-notes';

import ResultsDisplay from './ResultsDisplay';
import { statusCheck } from '../util';

import { VinSearchPropTypes } from '../propTypes';

const VinSearch = ({ bbService, mmrService, kbbService }) => {
  const [state, setState] = useState({
    vin: '',
    mileage: 0,
    zip: '',
    results: [],
    searching: false
  });

  /*
   * the setState we get from the hook does not do the shallow merge.
   * Nor does it take a second arg for callback
   */
  const updateState = (newState, cb = null) => {
    setState({
      ...state,
      ...newState
    });
    cb && cb();
  };

  const onTextInputChange = ({ target: { name, value } }) => {
    updateState({ [name]: value });
  };

  const onSubmitClick = () => {
    const { vin, mileage, zip } = state;
    updateState({ results: [], searching: true });
    Promise.all([
      bbService.getValuationsByVin({ vin, mileage }),
      mmrService.getValuationsByVin({ vin, mileage }),
      kbbService.getValuationsByVin({ vin, mileage, zip })
    ])
      .then(async ([bbResults, mmrResults, kbbResults]) => {
        updateState({ searching: false });
        const bb = await statusCheck(bbResults);
        if (bb.vehicleList.length === 0) {
          toast.notify('Black Book returned no results.', {
            position: 'bottom'
          });
        }
        const mmr = await statusCheck(mmrResults);
        if (mmr.items.length === 0) {
          toast.notify('Manheim returned no results.', {
            position: 'bottom'
          });
        }
        if (kbbResults.length === 0) {
          toast.notify('Kelly Blue Book® returned no results.', {
            position: 'bottom'
          });
        }
        const results = [
          ...bb.vehicleList,
          ...mmr.items.map(item => ({
            year: item.description.year,
            make: item.description.make,
            model: item.description.model,
            series: '',
            style: item.description.trim,
            extraCleanValue: null,
            cleanValue: item.wholesale.above,
            averageValue: item.wholesale.average,
            roughValue: item.wholesale.below,
            provider: 'Manheim Market Research'
          })),
          ...kbbResults
        ];

        updateState({ results });
      })
      .catch(e => {
        toast.notify('Error fetching values. Please try again later.', {
          position: 'bottom'
        });
      });
  };

  const onClearClick = () => {
    updateState({
      vin: '',
      mileage: 0,
      results: [],
      searching: false
    });
  };

  const onMileageFocus = () => {
    // does not seem possible to get the input using ref
    document.querySelector('#MileageInput-maskedInput').select();
  };

  const onVinFocus = e => {
    document.querySelector('#VinInput').select();
  };

  return (
    <Grid fluid className="template-page-wrapper page-wrapper__content-only">
      <Row>
        <Col sm={12} md={12} lg={12}>
          <Panel>
            <h4>VIN Search</h4>
            <div className="template-content-wrapper content-wrapper__content-only">
              <VinInput
                htmlId="VinInput"
                label="VIN"
                maxLength={17}
                name="vin"
                onChange={onTextInputChange}
                onFocus={onVinFocus}
                value={state.vin}
              />
              <NumericInput
                autoInsertCommas
                htmlId="MileageInput"
                name="mileage"
                label="Mileage"
                onChange={onTextInputChange}
                onFocus={onMileageFocus}
                value={state.mileage}
                maxLength={20}
              />
              <NumericInput
                htmlId="ZipInput"
                name="zip"
                label="Zip - required for KBB results"
                onChange={onTextInputChange}
                value={state.zip}
                maxLength={5}
              />
              <SubmitButton
                htmlId="SubmitButton"
                className="pull-right"
                onClick={onSubmitClick}
              >
                Submit
              </SubmitButton>
              <Button htmlId="ResetButton" onClick={onClearClick}>
                Reset
              </Button>
            </div>
          </Panel>
        </Col>
      </Row>
      {state.searching && <LoadingIndicator />}
      {state.results.length > 0 &&
        state.results.map((result, i) => (
          <ResultsDisplay result={result} key={i} /> // eslint-disable-line react/no-array-index-key
        ))}
    </Grid>
  );
};

VinSearch.propTypes = VinSearchPropTypes;

export default VinSearch;
