import toast from 'toasted-notes';

import { makeHeaders, statusCheck } from '../util';

export default () => {
  const accessToken = localStorage.getItem('access_token');
  const valuationsBaseUrl = localStorage.getItem('gateway_url');
  return {
    name: 'kbbService',
    getYears: callback => {
      return fetch(`${valuationsBaseUrl}/valuation-service/api/v2/kbb/years`, {
        method: 'GET',
        mode: 'cors',
        headers: makeHeaders(accessToken)
      })
        .then(resp => statusCheck(resp))
        .then(json =>
          callback({
            years: json.items.map(year => ({
              value: year.yearId,
              label: year.yearId
            }))
          })
        )
        .catch(() =>
          toast.notify('Error fetching years. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getMakes: ({ year }, callback) => {
      if (!year) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/kbb/makes?year=${year}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.items.length === 0) {
            callback([]);
            toast.notify('No makes found for this year.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            json.items.map(make => ({
              value: make.makeId,
              label: make.makeName
            }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching makes. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getModels: ({ year, make }, callback) => {
      if (!make) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/kbb/models?year=${year}&makeId=${make}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.items.length === 0) {
            callback([]);
            toast.notify('No models found for this make.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            json.items.map(model => ({
              value: model.modelId,
              label: model.modelName
            }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching models. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getStyles: ({ year, model }, callback) => {
      if (!model) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/kbb/vehicles?year=${year}&modelId=${model}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.items.length === 0) {
            callback([]);
            toast.notify('No styles found for this series.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            json.items.map(style => ({
              value: JSON.stringify({
                id: style.vehicleId,
                styleResults: json.items
              }),
              label: style.trimName
            }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching styles. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getValuations: ({ style, mileage, zip }, callback) => {
      let id, styleResults;
      try {
        const json = JSON.parse(style);
        id = json.id;
        styleResults = json.styleResults;
      } catch (e) {
        // do nothing
      }
      if (!id || !zip) {
        toast.notify('Missing style or zip.', {
          position: 'bottom'
        });
        callback({ searching: false });
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/kbb/prices?vehicleId=${id}&mileage=${mileage}&zipcode=${zip}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.prices.length === 0) {
            toast.notify('Kelly Blue Book® returned no results.', {
              position: 'bottom'
            });
            callback({ searching: false });
            return;
          }
          const selectedVehicle = styleResults.filter(
            vehicle => vehicle.vehicleId === id
          )[0];
          const { yearId, makeName, modelName, trimName } = selectedVehicle;
          callback({
            results: {
              year: yearId,
              make: makeName,
              model: modelName,
              series: '',
              style: trimName,
              extraCleanValue: json.prices.filter(
                price => price.condition === 'Excellent'
              )[0].configuredValue,
              cleanValue: json.prices.filter(
                price => price.condition === 'Very Good'
              )[0].configuredValue,
              averageValue: json.prices.filter(
                price => price.condition === 'Good'
              )[0].configuredValue,
              roughValue: json.prices.filter(
                price => price.condition === 'Fair'
              )[0].configuredValue,
              provider: 'Kelly Blue Book® Auction Values'
            },
            searching: false
          });
        })
        .catch(() => {
          callback({ searching: false });
          toast.notify('Error fetching values. Please try again later.', {
            position: 'bottom'
          });
        });
    },
    getValuationsByVin: async ({ vin, mileage, zip }) => {
      if (!zip) return Promise.resolve([]);
      const vinResults = await fetch(
        `${valuationsBaseUrl}/valuation-service/api/v2/kbb/vin/${vin}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => json.vinResults);

      if (vinResults.length === 0) return Promise.resolve([]);

      const vehiclePromises = vinResults.map(res =>
        fetch(
          `${valuationsBaseUrl}/valuation-service/api/v2/kbb/vehicles/id/${
            res.vehicleId
          }`,
          {
            method: 'GET',
            mode: 'cors',
            headers: makeHeaders(accessToken)
          }
        )
          .then(resp => resp.json())
          .then(json => json)
      );
      const vehicles = await Promise.all(vehiclePromises);
      const pricePromises = vehicles.map(vehicle =>
        fetch(
          `${valuationsBaseUrl}/valuation-service/api/v2/kbb/prices?vehicleId=${
            vehicle.vehicleId
          }&mileage=${mileage}&zipcode=${zip}`,
          {
            method: 'GET',
            mode: 'cors',
            headers: makeHeaders(accessToken)
          }
        )
          .then(resp => resp.json())
          .then(json => ({ prices: json.prices, vehicle }))
      );
      const results = await Promise.all(pricePromises);
      const retVal = results.map(res => {
        const { yearId, makeName, modelName, trimName } = res.vehicle;
        return {
          year: yearId,
          make: makeName,
          model: modelName,
          series: '',
          style: trimName,
          extraCleanValue: res.prices.filter(
            price => price.condition === 'Excellent'
          )[0].configuredValue,
          cleanValue: res.prices.filter(
            price => price.condition === 'Very Good'
          )[0].configuredValue,
          averageValue: res.prices.filter(
            price => price.condition === 'Good'
          )[0].configuredValue,
          roughValue: res.prices.filter(price => price.condition === 'Fair')[0]
            .configuredValue,
          provider: 'Kelly Blue Book® Auction Values'
        };
      });
      return Promise.resolve(retVal);
    }
  };
};
