import toast from 'toasted-notes';

import { makeHeaders, statusCheck } from '../util';

export default () => {
  const accessToken = localStorage.getItem('access_token');
  const valuationsBaseUrl = localStorage.getItem('gateway_url');
  return {
    name: 'bbService',
    getYears: callback => {
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/years`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json =>
          callback({
            years: json.yearList.map(year => ({ value: year, label: year }))
          })
        )
        .catch(() =>
          toast.notify('Error fetching years. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getMakes: ({ year }, callback) => {
      if (!year) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/drilldown/${year}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.makeList.length === 0) {
            callback([]);
            toast.notify('No makes found for this year.', {
              position: 'bottom'
            });
            return;
          }
          callback(json.makeList.map(make => ({ value: make, label: make })));
        })
        .catch(() =>
          toast.notify('Error fetching makes. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getModels: ({ year, make }, callback) => {
      if (!make) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/drilldown/${year}/${make}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.modelList.length === 0) {
            callback([]);
            toast.notify('No models found for this make.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            json.modelList.map(model => ({ value: model, label: model }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching models. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getSeries: ({ year, make, model }, callback) => {
      if (!model) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/drilldown/${year}/${make}/${model}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.seriesList.length === 0) {
            callback([]);
            toast.notify('No series found for this model.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            json.seriesList.map(series => ({ value: series, label: series }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching series. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getStyles: ({ year, make, model, series }, callback) => {
      if (!series) {
        callback([]);
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/drilldown/${year}/${make}/${model}/${series}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.styleList.length === 0) {
            callback([]);
            toast.notify('No styles found for this series.', {
              position: 'bottom'
            });
            return;
          }
          callback(
            json.styleList.map(style => ({
              value: style.name,
              label: style.name
            }))
          );
        })
        .catch(() =>
          toast.notify('Error fetching styles. Please try again later.', {
            position: 'bottom'
          })
        );
    },
    getValuations: (
      { year, make, model, series, style, mileage },
      callback
    ) => {
      if (!style) {
        toast.notify('Missing style.', {
          position: 'bottom'
        });
        callback({ searching: false });
        return;
      }
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/drilldown/valuation?year=${year}&make=${make}&model=${model}&series=${series}&style=${style}&mileage=${mileage}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      )
        .then(resp => statusCheck(resp))
        .then(json => {
          if (json.vehicleList.length === 0) {
            toast.notify('Black Book returned no results.', {
              position: 'bottom'
            });
            callback({ searching: false });
            return;
          }
          callback({ results: json.vehicleList[0], searching: false });
        })
        .catch(() => {
          callback({ searching: false });
          toast.notify('Error fetching values. Please try again later.', {
            position: 'bottom'
          });
        });
    },
    getValuationsByVin: ({ vin, mileage }) => {
      return fetch(
        `${valuationsBaseUrl}/valuation-service/api/v1/blackbook/vin/valuation/${vin}?mileage=${mileage}`,
        {
          method: 'GET',
          mode: 'cors',
          headers: makeHeaders(accessToken)
        }
      );
    }
  };
};
