import React from 'react';
import Col from '@cx/ui/Col';
import Row from '@cx/ui/Row';
import SimpleTable from '@cx/ui/SimpleTable';
import Panel from 'react-bootstrap/lib/Panel';

import { formatMoney } from '../util';
import { ResultsDisplayPropTypes } from '../propTypes';

const ResultsDisplay = ({ result }) => (
  <Row>
    <Col sm={12} md={12} lg={12}>
      <Panel>
        <div className="template-content-wrapper content-wrapper__content-only">
          <h4>
            {['year', 'make', 'model', 'series', 'style']
              .map(prop => result[prop])
              .join(' ')}
          </h4>
          <SimpleTable htmlId="SimpleTable" comfortable>
            <thead>
              <tr>
                <th scope="col">Extra Clean Value</th>
                <th scope="col">Clean Value</th>
                <th scope="col">Average Value</th>
                <th scope="col">Rough Value</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{formatMoney(result.extraCleanValue)}</td>
                <td>{formatMoney(result.cleanValue)}</td>
                <td>{formatMoney(result.averageValue)}</td>
                <td>{formatMoney(result.roughValue)}</td>
              </tr>
            </tbody>
          </SimpleTable>
        </div>
        <p>&nbsp;</p>
        <h5>Provider: {result.provider ? result.provider : 'Black Book'}</h5>
      </Panel>
    </Col>
  </Row>
);

ResultsDisplay.propTypes = ResultsDisplayPropTypes;

export default ResultsDisplay;
