import React, { useState, useEffect } from 'react';
import Grid from '@cx/ui/Grid';
import Col from '@cx/ui/Col';
import Row from '@cx/ui/Row';
import LoadingIndicator from '@cx/ui/LoadingIndicator';
import Panel from 'react-bootstrap/lib/Panel';

import ResultsDisplay from './ResultsDisplay';
import DrillDownSelects from './DrillDownSelects';
import { DrillDownPropTypes } from '../propTypes';

const initialState = { years: [], results: {}, searching: false };

const DrillDown = ({ model, service, title }) => {
  const [state, updateState] = useState({ ...initialState });

  /*
   * the setState we get from the hook does not do the shallow merge.
   * Nor does it take a second arg for callback
   */
  /* eslint-disable */
  const setState = (newState, cb = null) => {
    updateState({
      ...state,
      ...newState
    });
    cb && cb();
  };

  useEffect(() => {
    setState({ ...initialState }, () => service.getYears(setState));
  }, [service]);
  /* eslint-enable */

  const clearClick = () => {
    setState({ ...initialState }, () => service.getYears(setState));
  };

  return (
    <Grid fluid>
      <Row>
        <Col sm={12} md={12} lg={12}>
          <Panel>
            <h4>{title}</h4>
            <div>
              {state.years.length === 0 && <LoadingIndicator />}
              {state.years.length > 0 && (
                <DrillDownSelects
                  model={model}
                  service={service}
                  updateResults={setState}
                  years={state.years}
                  clear={clearClick}
                />
              )}
            </div>
          </Panel>
        </Col>
      </Row>
      {state.searching && <LoadingIndicator />}
      {Object.keys(state.results).length > 0 && (
        <ResultsDisplay result={state.results} />
      )}
    </Grid>
  );
};

DrillDown.propTypes = DrillDownPropTypes;

export default DrillDown;
