import { func, shape, number, string, oneOfType, arrayOf } from 'prop-types';

import { Config, UserManager } from 'ngc-ui-react-auth';

const model = func.isRequired;

const clear = func.isRequired;

const service = shape({
  name: string,
  getYears: func,
  getMakes: func,
  getModels: func,
  getSeries: func,
  getStyles: func,
  getValuations: func,
  getValuationsByVin: func
}).isRequired;

const title = string.isRequired;

const updateResults = func.isRequired;

const years = arrayOf(
  shape({
    value: oneOfType([string, number]),
    label: oneOfType([string, number])
  })
).isRequired;

export const DrillDownPropTypes = {
  model,
  service,
  title
};

export const DrillDownSelectsPropTypes = {
  model,
  service,
  updateResults,
  years,
  clear
};

export const ResultsDisplayPropTypes = {
  result: shape({
    year: number,
    make: string,
    model: string,
    series: string,
    style: string,
    extraCleanValue: number,
    cleanValue: number,
    averageValue: number,
    roughValue: number,
    provider: string,
    uvc: string,
    groupNumber: string,
    styleWithEngineDescription: string
  }).isRequired
};

export const AuthPropTypes = {
  config: shape(Config),
  userManager: shape(UserManager)
};

export const VinSearchPropTypes = {
  bbService: service,
  mmrService: service,
  kbbService: service
};
