import React from 'react';
import { useRedirect } from 'hookrouter';

import { AuthPropTypes } from '../propTypes';

const Login = ({ config, userManager }) => {
  localStorage.setItem('gateway_url', config.gatewayUrl);
  localStorage.setItem('access_token', userManager.token);
  useRedirect('/login', '/');
  return <div>Logging in ...</div>;
};

Login.propTypes = AuthPropTypes;

export default Login;
